html,
body {
  height: 100%;
}

#dashboard-wrapper {
	height: 100%;
}
#dashboard-home {
	height: 100%;
	width: 100%;
	display: table;
}

#page-container {
  display: table-row;
  width: 100%;
  height: 100%;
  #page-container-wrapper {
	display: table;
	width: 100%;
	height: 100%;
	&.row {
	  margin: 0;
	}
	#sidebar {
	  display: table-cell;
	  float: none;
	  width: 15%;
	  min-width: 200px;
	  &.collapsed {
		width: 0;
		min-width: 0;
		opacity: 0;
		display: none;
	  }
	}
	#page-contents {
	  display: table-cell;
	  float: none;
	  .alert-container {
		margin-bottom: 20px;
	  }
	}
  }
}

#sidebar {
  // width: 230px;
  float: none;
  background: #003254;
  font-family: $paragraph-font;
  height: 100%;
  padding: 0px 20px 20px;
  vertical-align: top;
  .nav > li > a {
  	display: block;
	padding: 23px 15px;
	font-size: 16px;
	&:hover {
	  text-decoration: none;
	}
  }
  .nav > li {
  	&:hover {
  		border-left: 5px solid #0081ff;
  	}
  }
}


#page-contents {
  // margin: 0 0 0 230px;
  padding: 20px;
}


