.content-page-heading {
  margin-top: 10px;
  margin-bottom: 20px;
  .page-headline {
	display: inline-block;
  }
  .content-page-actions {
	float: right;
  }
}

.text-muted {
  color: #ccc;
  font-size: small;
}

.fa-star {
  color: #FFD700;
}

.tab-pane-container {
  .nav-tabs {
	li {
	  cursor: pointer;
	  &.active {
		cursor: inherit;
	  }
	}
  }
}



.main-page-contents {
  h1 {
	margin: 0 0 20px;
  }
  .action-buttons {
	margin: 20px 0;
  }
  .nav-action-buttons {
	margin-bottom: 20px;
  }
}
.page-details {
  margin-bottom: 40px;
}


.highlighted-link {
  padding: 10px 15px;
  display: inline-block;
  border-radius: 5px;
  background: #fed587;
  a {
	color: white;
  }
  &.success {
	border: 2px solid #008000;
	color: #008000;
	background: #F3FFEB;
	padding-left: 40px;
	padding-right: 40px;
	font-size: 1.2em;
	a {
	  color: #008000;
	}
  }
}

.title-container {
  margin-bottom: 20px;
  &.no-bottom-margin {
	margin-bottom: 0;
  }
  .title-options {
	display: inline-block;
	margin-right: 10px;
  }
  .title-actions {
	display: inline-block;
	em {
	  font-size: 24px;
	}
	a {
	  color: $internal-border-color;
	  text-decoration: none;
	  margin-right: 10px;
	}
  }
  .permalink {
	a {
	  color: #008000;
	}
	margin: 10px 0;
  }
}



.action-success {
  .success-mark {
	font-size: 115px;
	color: #30CF4A;
  }
  p {
	&.headline {
	  font-size: 25px;
	  margin: 20px 0;
	}
  }
}


.dark-container {
  color: #DFE4ED;
  .nav-headline {
	margin: 10px 0;
	font-size: 14px;
	font-weight: bold;
  }
  .nav {
	li {
	  border-radius: 0;
	  border-bottom: 1px solid #545454;
	  //	  border-top: 1px solid #444;
	  margin-top: 0;

	  transition: 0.25s ease-out;
	  a {
		color: white;
		&:hover {
		  background: #1C508B;
		}
	  }
	}
	.active {
	  background: #505BCD;
	}
	.fas {
	  margin-right: 8px;
	  font-size: 1.3em;
	}
  }
}

.content-box {
  background: white;
  box-shadow: 4px 4px 8px #DADADA;
  .header {
	padding: 20px;
	background: #5DE45D;
	color: white;
	.headline {
	  font-size: 20px;
	}
  }
  .content {
	padding: 20px;
	.copy {
	  margin: 0;
	  font-size: 16px;
	  margin-bottom: 20px;
	}
  }
  .footer {
	.fineprint {
	  border-top: 1px solid #F9F9F9 ;
	  padding: 10px 20px;
	}
  }
}

.center-container {
  max-width: 700px;
  position: absolute;
  top: 30%;
  left: 50%;
  margin: -100px 0 0 -350px;
  .fineprint {
	text-align: center;
	a {
	  color: #D5D5D5;
	}
  }
}

.box-info {
  position: relative;
  padding: 15px;
  background: #fff;
  color: #5b5b5b;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  margin-bottom: 20px;
  border: 1px solid #cccccc;
  .icon-box {
	width: 65px;
	font-size: 30px;
	display: block;
	float: left;
  }
  .text-box h3 {
	padding: 0;
	margin: 5px 0;
	font-weight: 600;
	font-size: 20px;
	display: block;
	color: #4C5264;
  }
  .text-box p {
	font-size: 14px;
  }
  .progress.progress-xs {
	height: 5px;
	margin-top: 30px;
	margin-bottom: 20px;
	border-radius: 0px;
	box-shadow: none;
  }
}

footer {
  background: #444 !important;
  box-shadow: inset rgba(255, 255, 255, 0.1) 0 4px 0;
  padding: 0;
  a {
	color: #adadad;
  }
  .headline {
	color: #fff;
	font-weight: bold;
  }
  .text {
	color: #ccc;
	font-size: small;
  }
  ul {
	padding-left: 0px;
	list-style: none;
  }
  .small-footer {
	color: #ccc;
	.links {
	  a {
		text-transform: uppercase;
	  }
	}
  }

  .nav {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 900px) {
  #sidebar {
	.nav {
	  > li > a {
		text-align: center;
		font-size: 12px;
	  }
	  .fas {
		font-size: 30px;
		display: block;
		//border: 1px solid red;
		margin-right: 0;
	  }
	}
	.nav-headline {
	  font-size: 12px;
	  text-align: center;
	}
  }
}


.tt-query {
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}

.tt-hint {
  color: #999
}

.tt-dropdown-menu {
  width: 422px;
  margin-top: 12px;
  padding: 8px 0;
  background-color: #fff;
  border: 1px solid #ccc;
  border: 1px solid rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  -webkit-box-shadow: 0 5px 10px rgba(0,0,0,.2);
  -moz-box-shadow: 0 5px 10px rgba(0,0,0,.2);
  box-shadow: 0 5px 10px rgba(0,0,0,.2);
}

.tt-suggestion {
  padding: 3px 20px;
  font-size: 18px;
  line-height: 24px;
}

.tt-suggestion.tt-cursor {
  color: #fff;
  background-color: #0097cf;

}

.tt-suggestion p {
  margin: 0;
}



/* example specific styles */
/* ----------------------- */

.twitter-typeahead {
  display: block !important;
}

